.mainArticleList {
  position: relative;
  color: $colorWhite;
  margin-bottom: 20px;
  border-bottom: 1px dotted #cccccc;
  padding-bottom: 20px;
  figure {
    overflow: hidden; }
  @include sm {
    margin-bottom: 30px;
    border-bottom: none;
    padding-bottom: 0px; }

  p, a {
    @include sm {
      color: $colorWhite; } }
  .mainArticleList__image {
    z-index: 1;
    margin: 0 -15px;
    @media only screen and (min-width: 768px) {
      margin: 0px; }
    img {
      width: 100%;
      height: auto; }
    a {
      position: relative;
      display: block;
      &:after {
        background-image: linear-gradient(0deg, rgba(0,15,20,0.6) 15%,rgba(0,15,20,0.15) 52%,rgba(0,15,20,0) 70%);
        display: none;
        content: " ";
        height: 240px;
        width: 100%;
        position: absolute;
        bottom: 0px;
        @include sm {
          display: block; } } } }
  .mainArticleList__text {
    font-size: 1.6rem;
    @include sm {
      position: absolute;
      left: 55px;
      right: 35px;
      bottom: 30px;
      max-width: 690px;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5); } }

  .mainArticleList__kicker {
    font-size: 1.4rem;
    font-weight: 900;
    color: #ea545f;
    display: none;
    margin-top: 20px;
    @media only screen and (min-width: 768px) {
        display: block;
        color: #fff;
        margin-top: 0; } }

  .mainArticleList__headline {
    @include letterSpacing();
    font-size: 2.8rem;
    font-weight: 700;
    line-height: 110%;
    margin: 12px 0 0;
    @media only screen and (min-width: 768px) {
      font-size: 3.9rem;
      margin: 12px 0;
      text-shadow: 1px 1px 1px #000; } } }
