.mainArticle {
  font-family: source sans pro,sans-serif;
  position: relative;
  margin-bottom: 20px;
  border-bottom: 1px dotted #cccccc;
  padding-bottom: 20px;
  @include sm {
    margin-bottom: 30px;
    border-bottom: none;
    padding-bottom: 0px; }
  &--section {
    padding-bottom: 30px;
    display: flex;
    flex-direction: column-reverse;
    @media only screen and (min-width: 768px) {
      display: block; } }

  .mainArticle__image {
    z-index: 1;
    margin: 0px -15px;
    @include sm {
      margin: 0px; }
    img {
      width: 100%;
      height: auto; }
    a {
      position: relative;
      display: block; } }

  .mainArticle__text {
    font-size: 1.6rem;
    @include sm {
      position: absolute;
      left: 55px;
      right: 35px;
      bottom: 30px;
      max-width: 690px;
      text-shadow: 1px 1px 1px rgba(0,0,0,.8), -1px -1px 4px rgba(0,0,0,.8);
      max-width: 690px;
      @media only screen and (min-width: 768px) {
        a {
          color: #fff; } } } }

  .mainArticle__kicker {
    font-family: $fontSansSerif;
    font-size: 1.4rem;
    font-weight: 700;
    color: #ea545f;
    display: block;
    text-transform: uppercase;
    margin-top: 10px;
    &--section {
      margin-top: 0px; } }

  .mainArticle__headline {
    @include letterSpacing();
    font-size: 3.9rem;
    font-weight: 700;
    margin: 12px 0;
    text-shadow: 1px 1px 1px #000;
    &:hover {
      color: $colorGrey; }
    &--small {
      font-size: 3.2rem; } }

  .mainArticle__teaser {
    p {
      color: #fff;
      text-shadow: 1px 1px 1px rgba(0,0,0,.8), -1px -1px 4px rgba(0,0,0,.8);
      font-weight: 700; } } }

